body{
	font-weight: var(--regular);
	font-family: var(--bodyFontFamily);
	font-size: var(--bodySize, 16px);
	line-height: 1.625;
}

h1, h2, h3, h4, h5, h6,
.av-special-heading-tag{
	font-family: var(--headingFontFamily);
	color: var(--headingColor);
	font-weight: var(--headingWeight, var(--bold));
	text-transform: var(--headingTransform, none);
	line-height: var(--headingLineHeight);
	letter-spacing: var(--headingLetterSpacing, 0px);
	@media only screen and (max-width: 767px){
		font-size: var(--headingSizeMobile, var(--headingSize));
	}
	@media only screen and (min-width: 768px) and (max-width: 989px){
		font-size: var(--headingSizeTablet, var(--headingSize));
	}
	@media only screen and (min-width: 990px){
		font-size: var(--headingSize);
	}
}

h1, .h1 .av-special-heading-tag{
	--headingSize: 70px;
	--headingSizeTablet: 60px;
	--headingSizeMobile: 50px;
	--headingColor: var(--colorP1);
	--headingColorAlt: var(--colorP4);
	--headingLineHeight: 1.2;
	--headingWeight: var(--bold);
	--headingTransform: normal;
	--headingLetterSpacing: 0;
	
}

h2, .h2 .av-special-heading-tag{
	--headingSize: 50px;
	--headingSizeTablet: 45px;
	--headingSizeMobile: 40px;
	--headingColor: var(--colorP1);
	--headingColorAlt: var(--colorP4);
	--headingLineHeight: 1.2;
	--headingWeight: var(--bold);
	--headingTransform: normal;
	--headingLetterSpacing: 0;
}

h3, .h3 .av-special-heading-tag{
	--headingSize: 40px;
	--headingSizeTablet: 35px;
	--headingSizeMobile: 30px;
	--headingColor: var(--colorP1);
	--headingColorAlt: var(--colorP4);
	--headingLineHeight: 1.2;
	--headingWeight: var(--bold);
	--headingTransform: normal;
	--headingLetterSpacing: 0;
}

h4, .h4 .av-special-heading-tag{
	--headingSize: 25px;
	--headingSizeTablet: 25px;
	--headingSizeMobile: 25px;
	--headingColor: var(--colorP1);
	--headingColorAlt: var(--colorP4);
	--headingLineHeight: 1.2;
	--headingWeight: var(--bold);
	--headingTransform: normal;
	--headingLetterSpacing: 0;
}

h5, .h5 .av-special-heading-tag{
	--headingSize: 18px;
	--headingSizeTablet: 18px;
	--headingSizeMobile: 18px;
	--headingColor: var(--colorP1);
	--headingColorAlt: var(--colorP4);
	--headingLineHeight: 1.2;
	--headingWeight: var(--bold);
	--headingTransform: normal;
	--headingLetterSpacing: 0;
}

h6, .h6 .av-special-heading-tag{
	--headingSize: 15px;
	--headingSizeTablet: 15px;
	--headingSizeMobile: 15px;
	--headingColor: var(--colorP1);
	--headingColorAlt: var(--colorP4);
	--headingLineHeight: 1.2;
	--headingWeight: var(--bold);
	--headingTransform: uppercase;
	--headingLetterSpacing: calc(( 15px / 1000 ) * 100);
}
	
.av-subheading{
	font-family: var(--headingFontFamily);
	font-size: var(--subHeading, 18px);
	color: var(--subHeadingColor, var(--colorS2));
	line-height: 1.58;
	font-weight: var(--bold);
	display: block;
}
	
p{
	&:not(:last-child){
		margin: 0 0 0.85em 0;
	}
}

.header,
.ep-custom-menu-element .menu,
.ep-custom-menu-element .sub-menu,
.av-share-box-list,
.ep-social-bookmarks{
	--listGap: 0;
	--listInnerGap: 0;
	--listIcon: none;
}

ul li ul{
	--listIconColor: var(--colorP1);
}
ul li ul li ul{
	--listIconColor: var(--colorS1);
}
ol li ol{
	--orderedListStyle: lower-alpha;
	--orderedListIconColor: var(--colorP1);
}
ol li ol li ol{
	--orderedListStyle: lower-roman;
}
ul{
    margin: 0;
    padding: 0;
    li{
		margin: var(--listGap, 1em 0);
        padding: var(--listInnerGap, 0 0 0 1.4em);
        list-style: none;
        position: relative;
        &::before{
            content: var(--listIcon, '\e807');
    		font-family: var(--listIconFamily, var(--iconFontFamily));
			position: absolute;
			left: 0;
			top: var(--listIconPositionV, 0.5em);
			font-size: var(--listIconSize, 0.8em);
			line-height: 1;
			color: var(--listIconColor, var(--colorS2));
        }
    }
}
ol{
    margin: var(--orderedListGap, 0 0 0 1.2em);
    padding: 0;
	list-style: var(--orderedListStyle, decimal);
    li{
        padding-left: 0.2em;
		margin: 1em 0;
		&::marker{
			font-weight: var(--bold);
			color: var(--orderedListIconColor, var(--colorS2));
		}
    }
}

.avia-section{
	background-color: var(--sectionBgColor, var(--colorP4));
	color: var(--sectionColor, var(--colorP1));
	a{
		font-size: var(--linkSize, inherit);
		color: var(--linkColor, var(--colorS2));
		font-weight: var(--linkWeight, var(--bold));
		text-decoration: var(--linkDecoration, underline);
		transition: var(--linkTransition, color ease-in-out 0.2s);
		&:hover:not(:active){
			--linkColor: var(--linkColorHover, var(--colorE2));
			--linkDecoration: none;
		}
	}
}

.alternate_color{
	--sectionBgColor: var(--colorP1);
	--sectionColor: var(--colorP4);
	--subHeadingColor: var(--colorS1);
	h1, h2, h3, h4, h5, h6,
	.av-special-heading-tag{
		color: var(--headingColorAlt, var(--headingColor));
	}
	a{
		--linkColor: var(--colorP4);
		--linkColorHover: var(--colorP4);
	}
	ul{
		--listIconColor: var(--colorP4);
	}
	ul li ul{
		--listIconColor: var(--colorP4);
	}
	ul li ul li ul{
		--listIconColor: var(--colorS1);
	}
	ol{
		--orderedListIconColor: var(--colorP4);
	}
}
