html{
	--scrollBarWidth: 0px;
	--containerWidth: #{$container};
	--containerSingleWidth: #{$container-single};
	--defaultStrong: var(--bold);
}

// Colors, Fonts, Size
:root{
	--colorBlack: #000000;
	--colorWhite: #FFFFFF;

	// Primary
	--colorP1: #1A1A1A;
	--colorP2: #0D6EFD;
	--colorP3: #F8F9FA;
	--colorP4: var(--colorWhite);

	// Secondary
	--colorS1: #bbbbbb;
	--colorS2: #424242;

	// Tertiary


	// Extra


	// Fonts
	--headingFontFamily: 'Montserrat', sans-serif;
	--bodyFontFamily: 'Lato', sans-serif;
	--iconFontFamily: 'digitalarena-icons';

	// Font Weight
	--light: 300;
	--regular: 400;
	--medium: 500;
	--semibold: 600;
	--bold: 700;
}

/*
Hides all Flickity slides except for the first, until the Flickity is fully loaded (.flickity-enabled).
Additionally, this will not apply to Sliders that have the "watchCSS" parameter enabled (.ep-flickity-watch-css)
*/
.ep-flickity-slider:not(.flickity-enabled):not(.ep-flickity-watch-css) .ep-flickity-slide:not(:first-child) {
	display: none;
}
sup{
	font-size: 0.8em;
	vertical-align: super;
}
sub{
	font-size: 0.8em;
	vertical-align: sub;
}