.heading--home-hero{
    --headingSize: 109px;
    @media only screen and (max-width: 989px){
        --headingSize: 50px;
    }
    h1, .h1 .av-special-heading-tag{
        line-height: 1.1;
    }
}
.av-special-heading-tag a{
    --linkSize: var(--headingSize);
    --linkColor: var(--headingColor);
    --linkDecoration: none;
    --linkWeight: var(--headingWeight);
    --linkColorHover: var(--headingColor);
    --linkTransition: opacity ease-in-out 0.2s;
    &:hover:not(:active){
        opacity: 0.85;
    }
}