:root{
    --buttonTransitionProperty: box-shadow, border-radius, background-color, color;
    --buttonsGapV: 6px;
    --buttonsGapH: 12px;
}

div .avia-button,
.button,
.wp-block-button a,
.menu-button .menu-item-inner{
    font-size: var(--buttonSize, 16px);
    line-height: 1.3;
    font-family: var(--bodyFontFamily);
    font-weight: var(--semibold);
    padding: var(--buttonPadding, 16.5px 23px 16px);
    color: var(--buttonColor, var(--colorP4));
    background-color: var(--buttonBgColor, transparent);
    border-radius: var(--buttonBorderRadius, 2px);
    display: inline-block;
    transition-property: var(--buttonTransitionProperty);
    transition-timing-function: ease-in-out;
    transition-duration: 0.2s;
    text-decoration: none;
    &:hover{
        --buttonBorderRadius: 27px;
    }
}

div .avia-style-main,
.button,
.wp-block-button a,
.menu-button .menu-item-inner{
    --buttonBgColor: var(--colorP2);
    &:active{
        --buttonBgColor: var(--colorE4);
    }
}

div .avia-style-main,
div .avia-style-outline,
.button,
.wp-block-button a{
    @media only screen and (max-width: 480px){
        width: 100%;
        text-align: center;
        justify-content: center;
    }
}

div .avia-button{
    &.avia-size-small{
        --buttonSize: 14px;
        --buttonPadding: 8.5px 10px 8px;
    }
    &.avia-size-medium{
        --buttonPadding: 9.5px 23px 9px;
    }

    &.avia-style-outline{
        --buttonColor: var(--colorE5);
        box-shadow: inset 0 0 0 1px var(--colorE3);
        &:active{
            box-shadow: inset 0 0 0 2px var(--colorE3);
        }
    }
    &.avia-style-link{
        --buttonPadding: 0;
        --buttonColor: var(--colorS2);
        .avia_iconbox_title{
            text-decoration: underline;
        }
        &:hover:not(:active){
            .avia_iconbox_title{
                text-decoration: none;
            }
        }
    }

    &[class*='icon_select-yes']{
        display: inline-flex;
        align-items: center;
    }
    &.avia-icon_select-yes-right-icon{
        .avia_button_icon{
            margin-left: var(--buttonIconGap, 20px);
        }
    }
    &.avia-icon_select-yes-left-icon{
        .avia_button_icon{
            margin-right: var(--buttonIconGap, 20px);
        }
    }
}

.alternate_color{
    div .avia-button{
        &.avia-style-outline{
            --buttonColor: var(--colorP4);
            box-shadow: inset 0 0 0 1px var(--colorP4);
            &:active{
                box-shadow: inset 0 0 0 2px var(--colorP4);
            }
        }
        &.avia-style-link{
            --buttonColor: var(--colorP4);
        }
    }
}

.avia-buttonrow-wrap,
.wp-block-buttons{
    display: flex;
    flex-wrap: wrap;
    align-items: center;
    &.avia-buttonrow-left,
    &.is-content-justification-left{
        justify-content: flex-start;
    }
    &.avia-buttonrow-center,
    &.is-content-justification-center{
        justify-content: center;
    }
    &.avia-buttonrow-right,
    &.is-content-justification-right{
        justify-content: flex-end;
    }
    &.is-content-justification-space-between{
        justify-content: space-between;
    }
}
.avia-buttonrow-wrap{
    margin-left: calc( var(--buttonsGapH) * -1 );
    margin-right: calc( var(--buttonsGapH) * -1 );
    .avia-button{
        margin: var(--buttonsGapV) var(--buttonsGapH) !important;
    }
}
.wp-block-buttons{
    .wp-block-button{
        margin-top: var(--buttonsGapV);
        margin-bottom: var(--buttonsGapV);
        &:not(:last-child){
            margin-right: var(--buttonsGapH);
        }
    }
}
@media only screen and (max-width: 989px){
    .avia-buttonrow-wrap{
        &.avia-buttonrow-tablet-center{
            justify-content: center;
        }
        &.avia-buttonrow-tablet-right{
            justify-content: flex-end;
        }
        &.avia-buttonrow-tablet-left{
            justify-content: flex-start;
        }
    }
}
@media only screen and (max-width: 767px){
    .avia-buttonrow-wrap{
        &.avia-buttonrow-mobile-center{
            justify-content: center;
        }
        &.avia-buttonrow-mobile-right{
            justify-content: flex-end;
        }
        &.avia-buttonrow-mobile-left{
            justify-content: flex-start;
        }
    }
}